
import schedule from 'node-schedule';

let count = 0;
export const scheduleJobOnActivate = (swg: ServiceWorkerGlobalScope) => {
  console.log('sw-logic: schedule job on activate');
  swg.onactivate = (event) => {
    console.log('on activation');
    // Notification.requestPermission().then(showNotification);
    var job = schedule.scheduleJob('*/1 * * * *', function() {
      console.log('run job ', count);
      event.waitUntil(swg.registration.showNotification('Meowskis', {
        body: 'Sweet deal!'
      }));
    });
  }
}

export const onClickListener = (swgs: ServiceWorkerGlobalScope) => {
  swgs.onnotificationclick = (event) => {
    count++;
    console.log('count: ', count);
  }
}